  /*
  ==============================
  Main Header CSS
  ==============================
  */

  .site-navbar {
	margin-bottom: 0px;
	z-index: 1999;
	position: absolute;
	width: 100%;

	&.transparent {
		background: transparent;
	}
	&.absolute {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	}

	.site-logo {
		position: relative;
		top: 0px;

	}


	.site-menu-toggle {
		color: #000;
	}
	.site-navigation {
        margin-top: 17px;
        margin-top: 30px;
		&.border-bottom {
			border-bottom: 1px solid lighten($color-auxiliation, 80%)!important;
        }


		.site-menu {

			margin-bottom: 0;
			.active {
                font-weight: 600;
				> a {
                    color: $color-default;
                    font-weight: 600;
				}
			}
			// single page
			a {
				&.active {
                    color: $color-default;

				}
			}
			a {
				text-decoration: none!important;
				font-size: 16px;
			}
			> li {
				display: inline-block;
				> a {
					color: $color-auxiliation;
					text-decoration: none!important;
					&:hover {
                        color: $color-white;
                        background: rgba(30, 30, 30, 0.97);
					}
				}
            }

            .lastchildareaCustomer {
                padding-right: 0;
                min-width: 190px;

                    .areaCustomer{
                        color: $color-white!important;
                        font-weight: 600;
                        display: inline-block;
                        background: $color-default;
                        border-color: $color-default;
                        margin-bottom: 8px;

                        &:hover{
                            background: 0 0;
                            border-color: #3b9baa;
                            color: #484747!important;
                            font-weight: 600;
                        }
                    }

        }

			.has-children {
				position: relative;
				> a {
					position: relative;
					padding-right: 20px;

				}
				.dropdown {
					visibility: hidden;
					opacity: 0;
                    background: #070d1b;
                    width: 100%;
                    position: fixed;
                    top: auto;
					transition: 0.2s 0s;

					&.arrow-top {
						position: absolute;
						&:before {
							bottom: 100%;
							left: 20%;
							border: solid transparent;
							content: " ";
							height: 0;
							width: 0;
							position: absolute;
							pointer-events: none;
						}
						&:before {
							border-color: rgba(136, 183, 213, 0);
							border-bottom-color: $color-white;
							border-width: 10px;
							margin-left: -10px;
						}
					}


					a {
						text-transform: none;
						letter-spacing: normal;
						transition: 0s all;
						color: $color-auxiliation;
						&.active {
							color: $color-default;

						}
					}
					.active {
						> a {
							color: $color-default!important;
						}
					}
					> li {

                            display: inline-block;
                            width: 33%;
                            padding-left: 90px;
                            align-items: center;
						> a {
                            display: block;
                            width: 100%;
                            height: auto!important;
                            position: relative;
                            -moz-box-sizing: border-box;
                            box-sizing: border-box;
                            margin: 0;
                            padding: 11px 15px;
                            font-size: 21px;
                            text-align: left;
                            text-transform: none;
                            border-left: none;
                            border-right: none;
                            letter-spacing: 0;
                            color: $color-white;
                            cursor: pointer;

                            -webkit-transition: all 0.27s cubic-bezier(0,0,.58,1);
                            -moz-transition: all 0.27s cubic-bezier(0,0,.58,1);
                            -o-transition: all 0.27s cubic-bezier(0,0,.58,1);
                            -ms-transition: all 0.27s cubic-bezier(0,0,.58,1);
                            transition: all 0.27s cubic-bezier(0,0,.58,1);


							&:hover {
								color: $color-default;
							}
                        }

                        img{
                            position: absolute;
                            left: -76px;
                            float: left;
                            vertical-align: top;
                            width: 60px;

                        }

                        .description{
                            white-space: normal;
                            font-size: 12px;
                            margin-top: 10px;
                            width: 80%;
                            color: $color-white;
                        }



						&.has-children {
							> a {
								&:before {
									content: "\e315";
									right: 20px;
								}
							}
							> .dropdown, > ul {
								left: 100%;
								top: 0;
							}
							&:hover, &:active, &:focus {
								> a {
                                    background: #070d1b!important;
                                    color: $color-white;

								}
							}
                        }

                        &:hover{
                            .nav-link{
                                margin-left: 15px;
                                background: rgba(255,255,255,.09) !important;
                            }
                        }


					}
                }

                .mn-has-multi{
                    width: auto!important;
                    padding: 15px;
                    text-align: left;

                }

                .mn-sub {
                    left: 0;
                    background: rgba(30,30,30,.97);
                }

				&:hover, &:focus, &:active {
					cursor: pointer;
					> .dropdown {
						transition-delay: 0s;
						margin-top: 0px;
						visibility: visible;
						opacity: 1;


					}
				}
			}
        }
	}
	&.scrolled {
		background-color: $color-white;
		position: fixed;
		width: 100%;
		left: 0;
		right: 0;
		top: 0;
		margin-top: -130px;
		padding-top: 0!important;
		padding-bottom: 0!important;
        box-shadow: 0 8px 6px -6px rgba(0,0,0,.4);
		&.awake {
			margin-top: 0px;
			transition: .3s all ease-out;
		}
		&.sleep {
			transition: .3s all ease-out;
		}

		.site-menu-toggle {
			color: #000;
		}

		.site-navigation {
			.site-menu {
				li {
					a {
						&.active {
							color: $color-default;
						}
					}
				}
			}
		}

		.site-logo {
            top: -4px;
		}

	}
}


// mobile menu

.site-mobile-menu {
	width: 300px;
	position: fixed;
	right: 0;
	z-index: 9000;
	padding-top: 20px;
	background: $color-white;
	height: calc(100vh);

	transform: translateX(110%);
	box-shadow: -10px 0 20px -10px rgba(0,0,0,.1);
	transition: .3s all ease-in-out;

	.offcanvas-menu & {
		transform: translateX(0%);
	}

	.site-mobile-menu-header {
		width: 100%;
		float: left;
		padding-left: 20px;
		padding-right: 20px;

		.site-mobile-menu-close {
			float: right;
			margin-top: 8px;
			span {
				font-size: 30px;
				display: inline-block;
				padding-left: 10px;
				padding-right: 0px;
				line-height: 1;
				cursor: pointer;
				transition: .3s all ease;
				&:hover {
					color: $color-auxiliation;
				}
			}
		}
		.site-mobile-menu-logo {
			float: left;
			margin-top: 10px;
			margin-left: 0px;
			a {
				display: inline-block;
				text-transform: uppercase;
				img {
					max-width: 70px;
				}
				&:hover {
					text-decoration: none;
				}
			}
		}
	}

	.site-mobile-menu-body {
		overflow-y: scroll;
  	-webkit-overflow-scrolling: touch;
		position: relative;
		padding: 0 20px 20px 20px;

		height: calc(100vh - 52px);
		padding-bottom: 150px;

	}
	.site-nav-wrap {
		padding: 0;
		margin: 0;
		list-style: none;
		// float: left;

		// width: 100%;
		// height: 100%;
		position: relative;

		a {
			padding: 10px 20px;
			display: block;
			position: relative;
			color: $color-auxiliation;
			&:hover {
				color: $color-default;
			}
		}
		li {
			position: relative;
			display: block;
			&.active {
				> a {
					color: $color-default;
				}
			}
        }


		.arrow-collapse {
            &.collapsed:before{
                -webkit-transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
            }
			&:before {
				background: url(../../assets/images/arrow-down-sign-to-navigate.png) center no-repeat;
                z-index: 20;
                height: 100%;
                content: "";
                position: absolute;
                top: 50%;
                left: 90%;
                width: 15%;
                -ms-transform: translate(-50%,-50%) rotate(-180deg);
                transform: translate(-50%,-50%) rotate(-180deg);
                transition: .3s all ease;

            }


        }

		> li {
			display: block;
			position: relative;
			float: left;
			width: 100%;

			> a {
				padding-left: 20px;
				font-size: 20px;
			}
			> ul {
				padding: 0;
				margin: 0;
				list-style: none;
				> li {
					display: block;
					> a {
						padding-left: 40px;
						font-size: 16px;
					}
					> ul {
						padding: 0;
						margin: 0;
						> li {
							display: block;
							> a {
								font-size: 16px;
								padding-left: 60px;
							}
						}
					}
				}
			}
		}

	&[data-class="social"] {
		float: left;
		width: 100%;
		margin-top: 30px;
		padding-bottom: 5em;
		> li {
			width: auto;
			&:first-child {
				a {
					padding-left: 15px!important;
				}
			}
		}
	}

    }

}