  strong{
    color:$color-default
  }
}

/*
  ==============================
  Footer Area CSS
  ==============================
  */

  .footer-area {
    text-align: left;
    padding: 50px 0 0 14px;
    background: #222222;

    .row-relative{
        border-bottom: 1px solid $color-white;

        .col-lg-6{
            padding-left: 0;
        }

        .nav-info{
            ul{
                padding:0;
            }
        }

    .logo-footer {
        margin-bottom: 27px;
        display: block;
      }

    h4 {
      line-height: 22px;
      margin-bottom: 30px;
      color: $color-white;

      span {
        color: $color-default;
        font-size: 40px;
        font-weight: 700;
        text-transform: lowercase;
      }
    }

    strong{
        color: $color-default;
        display: block;
    }

    .col-lg-2{
        padding-left: 0;
    }

    .col-lg-4{
        padding-left: 0;
    }
    ul li {
      display: inline-block;
      a {
        color: $color-white;
        line-height: 30px;

        &:hover {
          text-decoration: none;
        }
      }
    }
    p {

      color: $color-white;
      font-size: 15px
    }

    .social{
        padding:0;
        li{
            a{
                width: 35px;
                height: 35px;
                text-align: center;
                line-height: 34px;
                border: 1px solid $color-white;
                border-radius: 50%;
                color: $color-white;
                font-size: 15px;

                &:hover, &:focus{
                    background: $color-default;
                    color: $color-white;
                    border-color: $color-default;
                }
            }
        }
    }
    }

    .footer-copy{
        p{

            margin-top: 30px;
            color: #fff;
            text-align: center;
            margin-bottom: 0;
        }
    }
  }

  /*
  ==============================
  Go Top CSS
  ==============================
  */

  .go-top {
    position: fixed;
    cursor: pointer;
    bottom: 30px;
    right: 20px;
    font-size: 25px;
    color: $color-white;

    background-color: #000;
    z-index: 1;
    display: none;
    width: 40px;
    text-align: center;
    height: 40px;
    line-height: 39px;
    border-radius: 50%;
    -moz-transition: .4s;
    -webkit-transition: .4s;
    transition: .4s;
    &:hover {
      color: $color-white;
      background: $color-default;
    }

    i{
      text-align: center;
      line-height:40px;
    }
  }