/*
  ==============================
  Pricing Area CSS
  ==============================
  */
.price-area{
    background: #f6f6f6;
    position: relative;
  .pricingTable {
    text-align: center;
    position: relative;
    padding-bottom: 25px;
    background:$color-white;
    border-radius: 4px;
    background-position: center center;
    box-shadow: 0 0 30px 0 rgba(67, 67, 67, 0.15);
    -moz-transition: .4s;
    -webkit-transition: .4s;
    -ms-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
    &:hover {
      -webkit-transform: translateX(5px);
      transform: translateX(5px);
    }
    &::after {
      content: "";
      width: 70px;
      height: 30px;
      background: $color-default;
      margin: 0 auto;
      position: absolute;
      top:110px;
      left: 0;
      width: 332px;
      right: 0;
      -webkit-clip-path: polygon(50% 100%, 0 0, 100% 0);
      clip-path: polygon(50% 100%, 0 0, 100% 0);
    }
    .title {
      padding: 20px 0;
      margin-bottom: 35px;
      background: $color-default;
      font-size: 25px;
      color: $color-white;
      line-height: 35px;
      text-transform: uppercase;
      border-radius: 4px;

      span{
          display: block;
          font-weight: 700;
          font-size: 30px;
      }
    }
    .price-value {
      margin-bottom: 25px;
      color: $color-default;
      padding-top: 20px;
    }
    .currency {
      display: inline-block;
      font-size: 25px;
      vertical-align: top;
      margin-top: 6px;
    }

    .btn{
        background:#ffa500;
        border:#ffa500;


        &:hover{
            background: $color-white;
            border:1px solid #ffa500!important;
            color:#ffa500;
        }

    }
  }


  .price-value {
    .amount {
      display: inline-block;
      font-size: 44px;
      font-weight: 500;
    }
    .month {
      display: block;
      font-size: 15px;
      line-height: 16px;
      color: #666;

    }
  }

  .pricingTable .pricing-content {
    border-top: 2px solid #eee;
    border-bottom: 2px solid #eee;
    margin-bottom: 30px;
    padding-top: 25px;
    padding-bottom: 25px;
    text-align: left;
    li {
      color: #8b8b8b;
      margin-top: 16px;
      &:first-child {
        margin-top: 0;
      }
      i {
        color: $color-default;
        margin-right: 4px;
      }

      &:last-child{
          opacity: 0;
      }
    }
  }
}

/*
  ==============================
  Pricing Area Internal CSS
  ==============================
  */

  .price-area-internal{
    background: #ffffff;
  .pricingTableInternal {
    text-align: center;
    position: relative;
    padding-bottom: 25px;
    background: #f6f6f6;
    min-height: 630px;
    border-radius: 4px;
    background-position: center center;
    box-shadow: 0 0 30px 0 rgba(67, 67, 67, 0.15);
    -moz-transition: .4s;
    -webkit-transition: .4s;
    -ms-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
    &:hover {
      -webkit-transform: translateX(5px);
      transform: translateX(5px);
    }

    .title {
        background: $color-default;
        font-size: 25px;
        color: #fff;
        text-transform: uppercase;
        border-radius: 4px;
        max-width: 500px;
        margin: 0 auto;
        position: relative;
        top: -25px;
        height: 90px;
        padding: 30px;
        box-shadow: 1px 1.732px 8px 0px rgba(0, 0, 0, 0.153);

      span{
        font-weight: 700;
        color: #ffa500;
        font-size: 20px;
        display: block;
        line-height: 34px;
      }
    }

    h4{
        color:$color-default;
    }

    .tit-small{
        color:$color-default;
        font-size: 32px;
        display: block;
        font-weight: 700;
        margin-top: 14px;
    }
    .price-value {
      margin-bottom: 25px;
      color: $color-default;
    }
    .currency {
      display: inline-block;
      font-size: 25px;
      vertical-align: top;
      margin-top: 6px;
      color:$color-default;
      font-weight: 700;
    }

    .btn{
        background:#ffa500;
        border:#ffa500;
        color: $color-white;
        margin-bottom: 20px;

        &:hover{
            background: $color-white;
            border:1px solid #ffa500!important;
            color:#ffa500;
        }

    }
  }


  .price-value-internal {
    .amount {
      display: inline-block;
      font-size: 60px;
      color:$color-default;
      font-weight: 700;
    }
    .month {
      display: inline-block;
      font-size: 15px;
      line-height: 16px;
      text-transform: capitalize;
      color:$color-default;

    }
  }

  .pricing-content-internal {
    margin-bottom: 30px;
    padding-top: 25px;
    padding-bottom: 25px;
    text-align: left;
    li {
      color: #8b8b8b;
      margin-top: 16px;
      &:first-child {
        margin-top: 0;
      }
      i {
        color: $color-default;
        margin-right: 4px;
      }

      h5{
          color:$color-default;
      }

      strong{
        color:$color-default;
        font-weight: 300;
      }


    }

    .list-opacity{
        opacity: 0;
    }

  }

}
