    }
    &::after {
      content: '';
      position: absolute;
      width: 5px;
      height: 100%;
      left: 10px;
      top: 0;
      background: $color-default;
      -o-transition: .4s;
      -ms-transition: .4s;
      -moz-transition: .4s;
      -webkit-transition: .4s;
      transition: .4s;
