  /*
  ==============================
  Tabs
  ==============================
  */

  .content-tabs{

  .tabset {
    > input[type="radio"], .tab-panel {
      display: none;
    }
    > {
      input {
        &:first-child:checked ~ .tab-panels > .tab-panel:first-child, &:nth-child(3):checked ~ .tab-panels > .tab-panel:nth-child(2), &:nth-child(5):checked ~ .tab-panels > .tab-panel:nth-child(3), &:nth-child(7):checked ~ .tab-panels > .tab-panel:nth-child(4), &:nth-child(9):checked ~ .tab-panels > .tab-panel:nth-child(5), &:nth-child(11):checked ~ .tab-panels > .tab-panel:nth-child(6) {
          display: block;
        }
      }
      label {
        position: relative;
        display: inline-block;
        padding: 10px 32px;
        margin-right: 6px;
        cursor: pointer;
        background: transparent;
        transition: background ease 0.2s;
        border: 1px solid #ddd;
        border-radius: 5px;
        margin-bottom: 0;
        font-size: 18px;
      }
      input:checked + label {
        border-color: $color-default;
        background: $color-default;
        color: $color-white;
      }
    }
  }

  .tab-panels{
      select{
        border: 1px solid #dddddd;
        border-radius: 5px;
        background-color: transparent;
        padding: 15px 30px;
        width: 100%;
        max-width: 456px;
        margin: 10px 0;
        font-size: 18px;
        height: 57px;
      }
      .box-calculation{
        border: 1px solid #b9b9b9;
        border-radius: 10px;
        margin-right: 10px;
        margin-left: 10px;
        max-width: 330px;
        margin-top: 20px;
        padding: 15px 20px;
        min-height: 222px;
        -moz-transition: .4s;
        -webkit-transition: .4s;
        -ms-transition: .4s;
        -o-transition: .4s;
        transition: .4s;

        h4{
            color:$color-default;
            border-bottom: 1px solid #b9b9b9;
            margin-bottom: 15px;

        }
      }
  }

  .tab-panel {
    margin-top: 35px;
  }

